.parent {
    display: flex;
    justify-content: center;
    cursor: pointer;
}



.container {
    max-width: 300px;
    margin: auto;
    background-color: #fff;
    border-radius: 10px;
    padding: 40px;
    text-align: center;
    box-shadow: 0 0 40px rgba(0, 0, 0, 0.3);
    position: absolute;
    top: 30%;
    z-index: 1;
    opacity: 0;
    transition: opacity 0.3s ease;
  }
  
  .show {
    opacity: 1;
  }
  
  .hide {
    opacity: 0;
    pointer-events: none; /* Disable interaction with hidden form */
  }
  


h1 {
    font-size: 24px;
    color: #333;
    margin-bottom: 30px;
}

.icon {
    width: 80px;
    height: 80px;
    margin-bottom: 20px;
}

form {
    display: flex;
    flex-direction: column;
}

.inputContainer {
    position: relative;
    margin-bottom: 20px;
}

input {
    width: 100%;
    padding: 10px;
    font-size: 16px;
    border-radius: 5px;
    border: 1px solid #ccc;
}

label {
    position: absolute;
    top: 50%;
    left: 10px;
    transform: translateY(-50%);
    background-color: #fff;
    padding: 0 5px;
    font-size: 14px;
    color: #999;
    pointer-events: none;
    transition: 0.2s;
}

input:focus+label,
input:not(:placeholder-shown)+label {
    top: 0;
    left: 0;
    transform: translate(10px, -50%);
    font-size: 14px;
    color: #333;
}

button {
    background-color: #333;
    color: #fff;
    padding: 10px 20px;
    font-size: 16px;
    border-radius: 5px;
    border: none;
    cursor: pointer;
    transition: background-color 0.2s;
}

button:hover {
    background-color: #555;
}


.crossIcon{
    position: absolute;
    top: 15px;
    right: 15px;
    display: flex;
    padding: 5px;
    cursor: pointer;
}


.selected{
    background-color: orange;
}

.buttonContainer button{
    margin-right: 10px;
}